}
.featured-cont span:before {
  content: "____";
  padding-right: 15px;
  position: relative;
  top: -7px;
}
.featured-cont a {
  text-decoration: none;
  margin-top: -5px;
  font-size: 14px;
}
.featured-cont a:before {
  content: "<";
  padding-right: 10px;
}

/* Two Featured Article
================================================== */
.container.two-cols {
  display: flex;
}

.featured-cont.cols {
  width: 50%;
}
.featured-cont.cols .col-sec.alt {
  text-align: right;
}
.featured-cont .col-sec.alt small:after {
  content: ">";
  font-weight: 600;
  padding-left: 10px;
}
.featured-cont .col-sec.alt small:before {
  display: none;
}
.featured-cont .col-sec.alt span:after {
  content: "____";
  padding-left: 15px;
  position: relative;
  top: -7px;
}
.featured-cont .col-sec.alt span:before {
  display: none;
}

