}

/* Responsive CSS
================================================== */
@media (max-width: 767px) {
  .menu-vertical {
    display: none;
  }

  .main-nav, .container-fluid.flex-parent {
    display: block;
  }
}
@media screen and (max-width: 480px) {
  .mobile-cont {
    display: flex;
    flex-direction: column;
  }
  .mobile-cont div {
    width: 100% !important;
  }
  .mobile-cont div:nth-child(2) {
    margin-top: 10px;
  }

  .navbar-brand {
    margin-right: 0;
  }

  nav a {
    padding: 0;
  }

  .country-cards {
    flex-direction: column;
  }
  .country-cards div {
    width: 100% !important;
    padding-top: 20px;
  }
}
.container {
  width: 960px !important;
}

@media (min-width: 768px) {
  .navbar-collapse {
    flex-basis: 0 !important;
    flex-grow: 0 !important;
    align-items: center;
  }
}
@media (max-width: 768px) {
  div#main-menu {
    padding: 20px;
    background: #fff2d7;
    height: 100%;
  }

  .featured-cont-form, .featured-cont {
    flex-direction: column;
  }

  .featured-cont-form div {
    width: 100% !important;
    margin-top: 20px;
    margin-left: 0 !important;
    flex-direction: column;
  }

  .img-grid-randon, .social-icons {
    flex-direction: row !important;
  }

  .featured-cont div {
    width: 100%;
    margin-top: -15px !important;
  }
}
@media only screen and (max-width: 767px) {
  .container {
    width: 320px !important;
  }
}